import joinHand from '../../assets/btn/btn_joinHand.svg'
import style from './style.module.scss'

export function SignUpButton(props) {
  return (
    <button className={`${style["sign-up"]} ${props.className ? props.className : style["sign-up_normal"]}`} aria-label=""></button>
  )
}

export function JoinHand(props) {
  return <img src={joinHand} alt="" className={`${style["join-hand"]} ${props.className}`}/>
}